import React from 'react'
import styled from 'react-emotion'
import ScaleLoader from 'react-spinners/ScaleLoader'

const MARGIN = '80px'

const Loading = styled('div')`
  margin-top: ${MARGIN};
  text-align: center;
  font-size: 30px;
`

const Wrapper = styled('div')`
  margin-top: ${MARGIN};
`

const Item = styled('div')`
  display: flex;
  align-items: center;
`

const Name = styled('div')``
const Licence = styled('div')`
  margin-right: 50px;
`

export const Licences = ({ isLoading, error, data }) => {
  if (isLoading) {
    return (
      <Loading>
        <ScaleLoader size={120} color="white" loading />
      </Loading>
    )
  }

  return (
    <Wrapper>
      <Item>
        <Name>react@0.12.3</Name>
        <Licence>MIT</Licence>
      </Item>
    </Wrapper>
  )
}



